import React from "react";
import { XCircle } from "lucide-react";
import "../../styles/dashboard.css";
import { useLanguage } from "../../i18n/LanguageContext";
import { UniversalLink as Link } from "../../database/UniversalLink";
import { useUniversalSearchParams } from "../../database/useUniversalSearchParams";

export function CheckoutCancelPage() {
  const { t } = useLanguage();
  const [searchParams] = useUniversalSearchParams();
  
  const planId = searchParams.get("plan");

  return (
    <div className="app-container">
      <div className="app-bg-glow"></div>

      <main className="app-main">
        <div className="app-content-column" style={{ maxWidth: "600px", textAlign: "center" }}>
          <div style={{ marginBottom: "48px" }}>
            <XCircle 
              size={80} 
              style={{ 
                color: "#ef4444", 
                marginBottom: "24px" 
              }} 
            />
            <h1 className="dashboard-title" style={{ color: "#ef4444" }}>
              {t("checkout.cancelTitle")}
            </h1>
            <p style={{ color: "#94a3b8", fontSize: "1.1rem", marginBottom: "32px" }}>
              {t("checkout.cancelSubtitle")}
            </p>
            
            {planId && (
              <div 
                style={{
                  padding: "16px",
                  borderRadius: "12px",
                  background: "rgba(239, 68, 68, 0.1)",
                  border: "1px solid rgba(239, 68, 68, 0.2)",
                  marginBottom: "32px",
                  color: "#ef4444",
                  fontSize: "0.9rem"
                }}
              >
                Cancelled plan: {planId}
              </div>
            )}

            <Link to="/buy" className="checkout-cta-btn">
              {t("checkout.backToBuy")}
            </Link>
          </div>
        </div>
      </main>
    </div>
  );
}